:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464d53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}

.App {
  background-color: var(--appColor);
  background-image: url("data:image/png;base64,");
  display: flex;
  flex-direction: column;
  gap: 6rem;
  mix-blend-mode: overlay;
  /* overflow: hidden; */
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

.strokeText {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.btn {
  padding: 9px;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms;
  background-color: #fff;
}

.btn:hover {
  cursor: pointer;
}

.blur {
  background: rgba(253, 120, 43, .69);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
}